main {
	margin: 0 auto;
	max-width: 100%;
  padding: 10px 10px;
}

:root {
  --primary: #0c0c0b;
  --secondary: #f3f1f1;
  --tertiary: #3d405b;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 
     'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  background-color:#f3f1f1;
  font-weight: bold;
  
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  
}

h1, h3 {
  font-weight: bold;
}

nav a:hover {
  text-decoration: none;
}

nav {
  background-color: #f3f1f1;
  width: 100%;
  float: none;
  padding: 25px 16px;
  text-decoration: none;
  text-align: center;
  margin: 0 auto;
  font-size: 20px;
  margin: 0 auto;
}

nav  li {
  cursor: pointer;
 
}

#header {
  background-image: linear-gradient(to right, rgb(78, 76, 76), rgb(187, 189, 169));
  width: 100%;
  top: 0;
  padding-top: 0px;
  text-align: center;
}
 #footer { 
    text-align: center; 
    margin: 0 auto;
    background-image: linear-gradient(to right, rgb(83, 79, 79), rgb(187, 189, 169));
    left: 0;
    bottom:0;
    right: 0;
    width: 100%;
} 

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.project-item {
  border-bottom: 1px solid #ccc;
  padding: 1rem 0;
}

.project-item:last-child {
  border-bottom: none;
}

.project-description {
  margin: 1rem 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 10;
  overflow: hidden;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #0e0d0d;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: var(--secondary);
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

